.module-tree {
  @include flex-center;
  flex-direction: column;

  @include toPhone {
    align-items: start;
  }

  &__line-vertical {
    height: 150px;
    width: 1px;
    background-color: var(--light-blue-50p);
  }
  
  &__join {
    @include flex-center;
    position: relative;
    width: max-content;
  }
  
  &__side {
    @include flex-center;
    position: absolute;
    width: max-content;

    left: 100%;
    flex-direction: row;
    
    &--reverse {
      left: initial;
      right: 100%;
      flex-direction: row-reverse;
    }

    &--bottom {
      top: -100%;
    }
  }
  
  &__number {
    @include size(68px);
    @include shadow;
    background-color: var(--white);
    border-radius: 100%;
    line-height: 130%;
    z-index: 1;
    
    color: var(--light-blue);
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 45px;
    letter-spacing: 0.01em;
    text-align: center;
    
    @include toPhone{
      @include size(38px);
      font-size: 26px;
      position: absolute; 
      top: 4px; 
      left: 88px;
    }
  }
  
  &__line-horizontal {
    height: 1px;
    width: 140px;
    background-color: var(--light-blue-50p);
  }
  
  &__point {
    @include size(16px);
    border-radius: 100%;
    background-color: var(--light-blue);
  }
  
  &__title {
    padding-inline: 20px;

    @include toPhone {
      padding-top: 10px;
      padding-inline: 10px;
      position: relative;
    }
  }
}

// Transitions

._transition-module-tree--height {
  height: 1px !important;
  transition: all 2s;

  &._anim-active {
    height: 150px !important;
  }
}

._transition-module-tree--width {
  width: 1px !important;
  transition: all 2s;

  &._anim-active {
    width: 140px !important;

    @include toLabtop {
      width: 70px !important;
    }

    @include toTablet {
      width: 35px !important;
    }
  }
}

._transition-module-tree--scale {
  transition: all 2s;

  transform: scale(0);
  margin: -100px;

  &._anim-active {
    transform: scale(1);
    margin: 0;
  }
}
