.card-person {
  $origin: &;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 200px;

  &__image {
    display: block;
    width: 110px;
    height: 110px;
    object-fit: cover;

    border-radius: 20px;
    margin-bottom: 20px;

    box-shadow: 10px 30px 50px -20px #96b6dc;

    transition: all 0.3s;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 23px;
    text-align: center;
    color: var(--dark-blue);
    margin-bottom: 6px;
  }

  &__block,
  &__container {
    transition: all 1s;
  }

  &__block {
    pointer-events: all;
  }


  &--disabled {
    width: auto;

    #{$origin}__image {
      height: 70px;
      width: 70px;
      box-shadow: none;
      margin: 0;
    }

    #{$origin}__block,
    #{$origin}__container {
      @include no-event;
      transform: scale(0);
      opacity: 0;
    }
  }
}
