.card-icon {
  $origin: &;
  display: flex;
  gap: 20px;
  width: 100%;

  & b {
    font-weight: 500;
    color: var(--dark-blue);
  }

  &__image {
    display: block;
    object-fit: cover;
    height: 100%;
    max-height: 90px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    gap: 7px;
    justify-content: center;
  }

  &__title {
    font-size: 23px;
    line-height: 100%;
  }

  &__text {
    color: var(--gray);
    line-height: 120%;
  }

  &--mini {
    display: inline-flex;
    width: max-content;
    text-align: start;
    gap: 12px;

    @include toPhone {
      gap: 6px;

      #{$origin}__image {
        height: 100%;
      }

      #{$origin}__text {
        font-size: 14px;
      }
    }
  }

  &--x2 {
    align-items: center;

    #{$origin}__image {
      @include size(38px);
    }

    #{$origin}__text {
      font-size: 20px;
      line-height: 20px;
      color: var(--dark-blue);
    }

    @include toPhone {
      #{$origin}__text {
        font-size: 18px;
      }
    }
  }

  &--border {
    max-width: 458px;
    width: 100%;

    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    box-shadow: 0 0 10px 0 rgb(150 182 220 / 35%);
    background: rgb(255 255 255 / 75%);
    padding: 25px;
    border-radius: 20px;

    #{$origin}__title {
      font-size: 23px;
      color: var(--dark-blue);
    }

    @include toPhone {
      padding: 20px 10px;
      gap: 10px;

       #{$origin}__title {
        font-size: 18px;
       }

       #{$origin}__text {
        font-size: 14px;
       }

      #{$origin}__image {
        @include size(60px);
      }
    }
  }

  &--border-2 {
    @extend .card-icon--border;
    padding-inline: 15px;
    gap: 13px;

    #{$origin}__title {
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 25px;
      line-height: 100%;
      letter-spacing: 0.01em;
      color: var(--dark-blue);
    }

    @include toPhone {
      #{$origin}__title {
        font-size: 20px;
      }

      #{$origin}__image {
        @include size(max-content);
      }
    }
  }

  @include toPhone {
    &__title {
      font-size: 18px;
    }
    
    &__text {
      font-size: 14px;
    }
  }
}
