.link {
  display: inline-flex;
  align-items: center;

  &__text {
    display: inline-block;
    font-weight: 500;
    text-decoration: none;
    color: var(--dark-blue);
    border-bottom: 1px solid var(--light-blue);
  }

  &__image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;

    & > * * {
      width: 100%;
      height: 100%;
      stroke: var(--dark-blue);
    }
  }
}
