.card-mini-circle {
  $origin: &;
  max-width: 300px;
  width: 100%;
  position: relative;

  display: flex;
  flex-direction: column;

  &__number {
    @include size(68px);
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-40%, -20%);

    font-family: var(--second-family);
    font-weight: 500;
    line-height: 130%;
    font-size: 45px;
    text-align: center;

    border-radius: 100%;
    background-color: var(--white);
    color: var(--light-blue);
    box-shadow: 0 4px 10px 0 rgb(150 182 220 / 30.2%);

    @include toLabtop {
      @include size(58px);
      font-size: 38px;
    }
  }

  &__image {
    display: block;
    width: 142px;
    height: 142px;
    object-fit: cover;

    border-radius: 100%;
    margin-bottom: 35px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 100%;

    color: var(--dark-blue);

    margin-bottom: 20px;
  }

  &__text {
    color: var(--text);
    line-height: 120%;
  }

  @include toPhone {
    &__image {
      margin-bottom: 18px;
    }

    &__title {
      font-size: 23px;
    }

    &__text {
      font-size: 14px;
    }
  }

  &--reverse {
    align-items: flex-end;

    #{$origin}__number {
      top: 0;
      right: 0;
      left: auto;
      transform: translate(20%, -20%);
    }

    #{$origin}__container {
      text-align: right;
    }
  }

  &--left {
    #{$origin}__container {
      @include flex-center;
      gap: 10px;
    }
  }
}
