.section-more-about-me {
  @include toPhone {
    margin-bottom: -180px;
  }

  &__fon-block {
    position: relative;
    height: 1680px;
  }

  &__header-block {
    $or: &;

    position: relative;
    margin-top: 150px;
    z-index: 1;

    // height: 200px;

    &__image {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90px;
    }

    &__container {
      margin-top: 95px;
    }

    &__text {
      font-size: 19px;
      line-height: 87%;
      letter-spacing: 0.02em;
      text-align: center;
      color: var(--text);

      @include toPhone {
        font-size: 16px;
        margin-bottom: 14px;
      }
    }

    &__main-text {
      font-family: var(--second-family);
      font-size: 45px;
      line-height: 100%;
      letter-spacing: 0.02em;
      text-align: center;
      color: var(--dark-blue);

      @include toPhone {
        font-size: 27px;
      }
    }

    @include toPhone {
      margin-top: 78px;
      height: 102px;
      margin-bottom: -14px;

      #{$or}__image {
        @include size(42px);
      }
      #{$or}__container {
        margin-top: 30px;
      }
    }
  }

  &__photo {
    margin-bottom: 24px;
  }

  &__fon {
    @include size(1680px);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);

    z-index: -1;

    background-image: url("./images/decor/bg_1.png");
    background-size: cover;

    border-radius: 100%;
    background-color: var(--white);
  }

  &__title {
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
  }

  &__cards {
    position: relative;

    &_1 {
      top: 87%;
      left: 20%;
      transform: translateX(-5%);
    }

    &_2 {
      top: 67%;
      left: 60%;
      transform: translateX(-65%);
    }

    &_3 {
      top: 35%;
      left: 10%;
    }

    &_4 {
      top: 6%;
      left: 92%;
      transform: translateX(-100%);
    }

    @include toDesktop {
      &_3 {
        transform: translateX(-25%);
      }

      &_4 {
        transform: translateX(-80%);
      }
    }

    @include toLabtop {
      &_1 {
        left: 3%;
      }

      &_2 {
        transform: translateX(-36%);
      }

      &_3 {
        top: 47%;
      }

      &_4 {
        top: 0;
      }
    }

    @include toPhone {
      display: flex;
      flex-direction: column;
      gap: 14px;
      margin-top: -620px;
      padding-top: 500px;
    }

    & > * {
      width: max-content;
      position: absolute;

      @include toPhone {
        width: 100%;
        position: relative;
        transform: none !important;
        top: 0 !important;
        left: 0 !important;

        & > * {
          max-width: 100%;
        }
      }
    }
  }

  &__statistics {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: space-between;
    padding: 156px 275px 0 148px;
    gap: 20px;

    @include toDesktop {
      padding: 154px 0 0;
      gap: 40px;
    }

    @include toLabtop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 0;
      padding: 92px 0 20px;
    }

    @include toPhone {
      padding: 0;
    }
  }

  &__visitca {
    transform: translateY(86px);

    @include toLabtop {
      transform: none;
      flex-direction: column;
    }
  }

  &__button {
    // transform: translateY(-15px);
    margin-bottom: 15px;
    z-index: 2;

    @include toDesktop {
      transform: none;
      margin-top: 84px;
    }

    @include toLabtop {
      margin-top: 68px;
    }
  }
}

