.main-photo {
  @include size(100%);
  background-color: rgb(237 237 237 / 42%);

  &,
  & * {
    @include flex-center;
    box-sizing: content-box;
    border-radius: 100%;
  }

  &__1 {
    @include size(85%);
    background-color: rgb(230 230 230 / 42%);
  }
  
  &__2 {
    @include size(80%);
    background-color: rgb(221 221 221 / 42%);
  }

  &__image {
    @include size(75%);
    background-image: url('./images/photos/self_1.jpg');
    background-size: cover;
    background-position: center;
  }
}
