.main-title {
  /* #region Default */
  position: relative;

  display: flex;
  flex-direction: column;
  gap: 25px;

  z-index: 1;

  &__text {
    font-size: 20px;
    line-height: 87%;
    color: var(--text);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 300;
    font-size: 80px;
    line-height: 80%;
    color: var(--dark-blue);
    white-space: pre-line;
    text-wrap: nowrap;
  }

  &__content {
    font-size: 18px;
    line-height: 120%;
  }

  &__button {
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translate(-10%, 110%) rotate(-15deg);
  }

  /* #endregion */

  @include toDesktop {
    &__text,
    &__content {
      font-size: 15px;
    }

    &__title {
      font-size: 66px;
    }
  }

  @include toTablet {
    &__title {
      font-size: 48px;
    }
  }

  @include toPhone {
    gap: 15px;

    &__title { 
      margin-bottom: 10px;
    }
  }
}
