.link-wrapper {
  display: block;
  transition: all var(--transition-time);

  &:hover {
    transform: scale(1.01);
    filter: drop-shadow(0 10px 60px var(--light-blue-50p));
    z-index: 2;
  }
  
  &--effect-1 {
    &:hover {
      transform: scale(1.01);
      filter: none;
      z-index: 2;
    }
  }
}
