.section-tariffs {
  &__title {
    margin-bottom: 70px;
    
    @include toLabtop {
      margin-bottom: 20px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 32% 36% 32%;
    width: 100%;
    padding-inline: 50px;
  }

  &__item {
    padding-inline: 25px;
    width: 100% !important;

    &:first-child {
      padding-top: 25px;
      padding-left: 0;

    }
      
    &:last-child {
      padding-top: 25px;
      padding-right: 0;
    }

    @include onLabtop {
      transition: all var(--transition-time);

      &:hover {
        filter: drop-shadow(0 0 40px var(--white));
        transform: scale(1.01);
      }
    }
  }
  
  @include toDesktop {
    &__list {
      padding-inline: 18px;
    }

    &__item {
      padding-inline: 9px;
    }
  }

  @include toLabtop {
    &__list {
      display: grid;
      grid-template-columns: repeat(1, 100%);
      width: 100%;
      gap: 30px;
    }

    &__item {
      padding: 0 !important;
    }
  }

  @include toPhone {
    &__list {
      padding: 0;
    }
  }
}
