@import "libs/imports";

* {
  box-sizing: border-box;
}

hr {
  border-color: var(--light-blue);
}

.to-up {
  @include flex-center;
  @include size(60px);
  
  padding: 10px;
  transition: opacity var(--transition-time);
  opacity: .2;
  border: 1px solid transparent;
  border-radius: 100%;

  &__image {
    height: 100%;
  }
  
  &:hover {
    border: 1px solid var(--light-blue-50p);
    opacity: 1;
  }

  @include toLabtop {
    @include size(46px);
    padding: 4px;
  }
}

a.to-link {
  font-weight: 500;

  &:hover {
    text-decoration: underline;
  }
}

._fix-hook {
  padding-top: 100px;
  margin-top: -100px;
}

._fix-for-decor {
  @include toLabtop {
    padding-right: 30px;
  }

  @include toPhone {
    padding: 0;
  }
}

._no-event {
  @include no-event;
}

._relative {
  position: relative;
}

._absolute {
  position: absolute;
}

.input {
  &__title {
    color: var(--gray);
  }

  &__d {
    display: block;
    width: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    height: 23px;
    line-height: 23px;
    font-size: 23px;
    border-bottom: 1px solid var(--light-blue);
  }
}

::selection {
  background-color: var(--light-blue-50p);
  color: var(--white);
}

.page {
  scroll-behavior: smooth;
  height: 100%;

  color: var(--text);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 110%;

  &__body {
    width: 100%;
    height: 100%;
    margin: 0 auto;

    position: relative;
    background-color: var(--bg);
    transition: color var(--transition-time),
      background-color var(--transition-time);

    @include toLabtop {
      transition: all var(--transition-time);
    }

    &--stop-scroll {
      overflow: hidden;

      padding-right: var(--scroll-bar-size);
    }
  }

  &__wrapper {
    max-width: 1680px;
    margin: auto;
    display: flex;
    flex-direction: column;
  }

  &__header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;

    z-index: 100;
  }

  &__menu {
    @include no-event;
    position: sticky;
    overflow: hidden;
    display: flex;
    justify-content: end;
    z-index: 10;
    margin-bottom: -162px;
    padding-right: 50px;
    padding-bottom: 50px;
    bottom: 0;

    height: 100dvh;
    margin-top: -100dvh;

    &__container {
      @include flex-center;
      flex-direction: column-reverse;
      gap: 20px;
      place-self: flex-end;
      pointer-events: all;
    }

    @include toDesktop {
      margin-bottom: -132px;
      padding-right: 20px;
      padding-bottom: 20px;
    }

    @include toPhone {
      margin-bottom: -262px;
    }
  }

  &__main {
    flex: 1 1 auto;

    display: flex;
    flex-direction: column;
    gap: 60px;

    min-height: 100%;
    padding: 0 50px;
    margin-bottom: 162px;

    @supports (overflow: clip) {
      overflow: clip;
    }

    @supports not (overflow: clip) {
      overflow-x: hidden;
    }

    position: relative;

    &::before {
      @include fullscreen;
      @include no-event;
      content: "";
      box-shadow: inset 0 0 42px 8px var(--bg);
      z-index: 1;
    }

    @include toDesktop {
      margin-bottom: 132px;
      padding: 0 20px;
    }

    @include toPhone {
      margin-bottom: 262px;
      padding: 0 15px;
    }
  }
}

/* + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + */

.bigger {
  --size-bigger: 174%;

  width: var(--size-bigger);
  padding-bottom: var(--size-bigger);

  position: absolute;
  place-self: center;
  bottom: 60px;

  background-color: var(--white);
  border-radius: 100%;
  z-index: -2;

  @include shadow;

  animation-name: bigger-scale-animation;
  animation-timing-function: linear;
  animation-direction: alternate;
  animation-fill-mode: forwards;
  animation-timeline: view(block);
  animation-range-start: 0;
  animation-range-end: 20%;

  @include toDesktop {
    --size-bigger: 230%;
    animation-range-end: 15%;
  }

  @include toLabtop {
    --size-bigger: 350%;
  }

  @include toPhablet {
    --size-bigger: 474%;
  }

  @include toPhone {
    --size-bigger: 770%;
    animation-range-end: 18%;
  }
}

@keyframes bigger-scale-animation {
  from {
    transform: scale(0.1);
  }

  to {
    bottom: 60px;
    transform: scale(1);
  }
}

// ----

.circle-decore {
  width: 764px;
  height: 744px;
  position: absolute;
  z-index: 0;
  border: 1px solid var(--light-blue);
  border-radius: 100%;
  top: -162px;

  @include toDesktop {
    @include size(554px);
    top: -126px;
  }

  @include toPhablet {
    @include size(687px);
    top: -160px;
  }

  @include toPhone {
    @include size(384px);
  }
}

.block-circle {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  position: relative;

  @include size(834px);

  padding: 42px;
  border-radius: 100%;
  background-color: var(--white);

  &__image {
    margin-bottom: 60px;
  }

  &__image-2 {
    @include toPhone {
      height: 90px;
      align-self: flex-end;
      margin-top: -30px;
    }
  }

  &__container {
    @include flex-center;
    flex-direction: column;
    text-align: center;
  }

  &__text {
    font-size: 19px;
    line-height: 87%;
    letter-spacing: 0.02em;
  }

  &__title {
    font-family: var(--second-family);
    font-size: 45px;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: var(--dark-blue);
  }

  @include toLabtop {
    @include size(712px);

    &__title {
      font-size: 36px;
    }

    &__image {
      height: 50px;
    }
  }

  @include toPhone {
    position: relative;
    margin-top: 200px;
    width: 100%;
    height: max-content;
    border-radius: 30px;
    padding: 0 15px;

    &::after {
      @include size(500px);
      content: "";
      display: block;
      background: var(--white);
      position: absolute;
      top: 0%;
      transform: translateY(-20%);
      z-index: -1;
      border-radius: 100%;
    }

    &__image {
      margin-bottom: 14px;
      height: 70px;
    }

    &__decor {
      margin-bottom: 38px;
    }

    &__text {
      font-size: 16px;
      margin-bottom: 12px;
    }

    &__title {
      font-size: 21px;
    }
  }
}

.box-shadow {
  box-shadow: 0 3px 4px 0 #1f211d0a;
}

.shadow-gradient {
  position: relative;

  & * {
    z-index: 1;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    z-index: 0;

    filter: blur(150px);
    background: linear-gradient(
      360deg,
      rgb(150 182 220 / 23%) 0%,
      rgb(166 239 221 / 23%) 100%
    );
  }
}

.koma {
  display: block;
  width: 86px;
  height: 74px;

  @include toDesktop {
    width: 64px;
    height: 56px;
  }

  @include toLabtop {
    width: 44px;
    height: 38px;
  }
}

// ---

.img-decore {
  @include size(294px);

  border-radius: 100%;
  overflow: hidden;
  background-image: url("./images/decor/decore_2.jpg");
  mix-blend-mode: overlay;
  background-size: cover;

  @include toDesktop {
    @include size(122px);

    transform: translateY(40px);
    margin-right: 74px;
  }

  @include toLabtop {
    @include size(194px);

    margin: auto;
  }
}

.splide {
  $origin: &;
  filter: drop-shadow(0 3px 10px rgb(31 33 29 / 14%));
  margin-bottom: 50px;
  height: max-content;

  &__slide {
    cursor: pointer;
  }

  &__track {
    & article {
      height: 100%;
    }
  }

  &--fix {
    margin-right: 33px;

    #{$origin}__slide {
      margin-inline: 11px !important;

      & > * {
        width: calc(100%) !important;
        max-width: calc(100%) !important;
      }
    }

    @include toTablet {
      margin-right: 19px;

      #{$origin}__slide {
        margin-inline: 0 !important;
        margin-right: 10px !important;

        & > * {
          height: 100%;
        }
      }
    }

    @include toPhone {
      margin-right: 0;
    }
  }

  $transform-translate-y: 20px;

  &__arrow {
    width: 40px;
    height: 40px;
    background-color: var(--white);
    border: 1px solid var(--light-blue);
    top: 100%;
    bottom: inherit;
    transform: translateY($transform-translate-y - 6);

    & > svg {
      fill: var(--light-blue);
    }

    transition: background-color 0.1s, box-shadow 0.2s, border 0.2s,
      opacity 0.2s;

    @include onLabtop {
      &:hover {
        opacity: 1;
        box-shadow: 0 0 40px -5px var(--light-blue);
      }

      &:active {
        transform: translateY($transform-translate-y - 6) scale(0.9);
        background-color: var(--light-blue);
        color: var(--white);
        border: 6px solid var(--light-blue);

        & > svg {
          fill: var(--white);
        }
      }
    }

    @include toPhone {
      display: none;
    }
  }

  &__item {
    border-radius: 20px;
    object-fit: contain;
  }

  &__pagination {
    transform: translateY($transform-translate-y);
    gap: 10px;
    bottom: inherit;
    top: 100%;

    &__page {
      @include size(10px);
      border: 1px solid var(--light-blue);
      border-radius: 100%;
    }

    @include toLabtop {
      transform: translateY($transform-translate-y) scale(0.75);
    }
  }

  .splide__pagination__page.is-active {
    background-color: var(--light-blue);
    transform: scale(1.3);
  }
}

.contacts {
  $origin: &;
  text-align: end;
  margin-top: 23px;

  &__name {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 26px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: var(--dark-blue);
    margin-bottom: 66px;
    order: 1;
  }

  &__links {
    display: flex;
    flex-direction: column-reverse;
    gap: 10px;
  }

  &__link {
    display: flex;
    justify-content: end;
    align-items: center;
    gap: 6px;
    margin-bottom: 10px;

    &:hover {
      text-decoration: underline;
    }

    & > *:link {
      font-size: clm_px(14, map-get($breakpoints, tablet), 20);
      text-transform: lowercase;
      color: var(--light-blue);
    }
  }

  @include toLabtop {
    #{$origin}__name {
      margin-bottom: 22px;
    }

    #{$origin}__links {
      flex-direction: column;
    }
  }

  @include toPhone {
    #{$origin}__name {
      font-weight: 700;
      font-size: 20px;
      margin-bottom: 22px;
    }

    #{$origin}__links {
      flex-direction: column-reverse;
    }

    #{$origin}__link {
      & > *:link {
        font-size: clm_px(16, map-get($breakpoints, phone), 20);
      }
    }
  }
}

// ---------------

.section-timer {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 25px;
    line-height: 120%;
    text-align: center;
    color: var(--dark-blue);

    margin-bottom: 30px;
  }
}

.section-bonus {
  &__title {
    margin-bottom: 60px;
  }

  &__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 60px;
  }

  @include toLabtop {
    &__list {
      gap: 25px;
    }
  }

  @include toTablet {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  @include toPhone {
    &__title {
      margin-bottom: 30px;
    }

    &__list {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

.section-modal {
  @include flex-center;
  height: 100%;
  padding: 30px;

  &__container {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 20px;
  }

  &__bg {
    @include fullscreen;
    background-color: var(--dark-blue);
    filter: blur(200px);
  }

  &__screen {
    @include flex-center;
    height: 100%;
  }

  &__content {
    border-radius: 20px;
    z-index: 1;
    filter: drop-shadow(0 0 40px var(--light-blue));
  }
}
