.title--mini {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;
  
  &__number {
    font-size: 16px;
    line-height: 18px;
    color: var(--light-blue);
    margin-bottom: 10px;
  }
    
  &__main-text {
    font-family: var(--second-family);
    font-size: 28px;
    line-height: 28px;
    color: var(--dark-blue);
    margin-bottom: 20px;
  }
      
  &__text {
    font-size: 16px;
    color: var(--text);
  }

  &__container {
    display: flex;
    flex-direction: column-reverse;
  }
}
