.block-circle-mini {
  $origin: &;
  position: relative;

  @include flex-center;
  @include size(420px);

  @include toDesktop {
    @include size(355px);
  }

  flex-direction: column;
  gap: 20px;

  text-align: center;
  padding: 38px;

  border-radius: 100%;
  border: 1px solid var(--white);
  background-color: var(--light-blue);
  box-shadow: 0 4px 10px 0 rgb(150 182 220 / 30.2%);

  z-index: 1;

  &__number {
    width: 68px;
    height: 68px;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(100%);

    @include toDesktop {
      transform: translateY(25%);
    }

    font-family: var(--second-family);
    font-weight: 500;
    line-height: 120%;
    font-size: 45px;
    text-align: center;

    border-radius: 100%;
    background-color: var(--white);
    color: var(--light-blue);
    box-shadow: 0 4px 10px 0 rgb(150 182 220 / 30.2%);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    line-height: 100%;
    color: var(--white);
  }

  &__text {
    line-height: 120%;
    color: var(--white);
  }

  &--gray {
    border: 1px solid var(--light-blue-50p);
    background-color: rgb(150 182 220 / 10%);

    #{$origin}__title {
      color: var(--dark-blue);
    }

    #{$origin}__text {
      color: var(--text);
    }
  }

  &--white {
    @extend .block-circle-mini--gray;

    background-color: var(--white);
  }

  @include toPhone {
    transform: scale(0.8);

    &__number {
      left: 30px;
      top: -20px
    }
  }
}
