.card-image {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  position: relative;

  max-width: 405px;
  width: 100%;
  height: 366px;

  overflow: hidden;

  border-radius: 30px;
  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 10%);
  padding: 20px;

  &__image {
    display: block;
    position: absolute;
    inset: 0;

    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  $blur: 3px;

  &__container {
    width: 100%;
    border-radius: 25px;
    padding: 30px 40px;
    -webkit-backdrop-filter: blur($blur);
    backdrop-filter: blur($blur);
    background: rgb(255 255 255 / 70%);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 100%;

    color: var(--dark-blue);

    margin-bottom: 20px;
  }

  &__text {
    color: var(--text);
    line-height: 120%;
  }

  @include toPhone {
    padding: 18px;
    height: 288px;

    #{$origin}__title {
      font-size: 22px;
      margin-bottom: 14px;
    }

    #{$origin}__text {
      font-size: 15px;
    }

    #{$origin}__container {
      padding: 22px 28px 12px;
    }
  }

  &--big {
    position: relative;
    max-width: 410px;
    height: 450px;

    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;

      width: 90px;
      height: 90px;

      background-image: url("./images/icons/gray__love.svg");
      background-size: cover;
      background-repeat: no-repeat;
      filter: drop-shadow(0 0 10px rgb(255 255 255 / 50%));
    }
  }
}
