.card-mini-short {
  max-width: 300px;
  width: 100%;
  position: relative;

  &__image {
    display: block;
    width: 142px;
    height: 142px;
    object-fit: cover;
    float: right;

    border-radius: 100%;
    margin-bottom: 35px;
  }

  &__title {
    font-family: var(--second-family);
    font-style: italic;
    font-size: 28px;
    line-height: 101%;
    letter-spacing: 0.02em;
    text-align: right;
    color: var(--dark-blue);
  }

  @include toPhone {
    &__image {
      margin-bottom: 18px;
    }
  }
}
