.section-recommendations {
  position: relative;
  margin-bottom: -180px;
  
  &__wrapper {
    row-gap: 40px;
  }

  &::before {
    content: "";

    @include size(100vw);
    position: absolute;
    top: -128px;

    display: block;
    border-radius: 100%;
    background: var(--bg);
    z-index: -3;
  }

  &__title {
    text-align: center; 
    font-style: normal;
    max-width: 700px;
    margin: auto;
  }

  &__decor {
    margin-bottom: -182px;
  }

  &__rec {
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 40px;
  }

  &__rec-2 {
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 60px;
  }

  @include toLabtop { 
    margin-bottom: -100px;
  }

  @include toPhone {
    margin-bottom: 100px;

    &__wrapper {
      row-gap: 26px;
    }

    &__decor {
      margin-bottom: 40px;
    }

    &__rec {
      justify-content: center;
      row-gap: 20px;
    }

    &__rec-2 {
      justify-content: center;
      row-gap: 20px;
    }

    &__img-decore {
      @include size(122px);
      position: absolute;
      top: -420%;
      left: 0;
      z-index: -1;
    }
  }
}
