.card-mini {
  $origin: &;
  max-width: 330px;
  width: 100%;

  &__image {
    display: block;
    width: 142px;
    height: 142px;
    object-fit: cover;

    border-radius: 20px;
    margin-bottom: 35px;

    @include toPhone {
      margin-bottom: 18px;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 100%;

    color: var(--dark-blue);

    margin-bottom: 20px;

    @include toPhone {
      font-size: 20px;
      margin-bottom: 18px;
    }
  }

  &__text {
    color: var(--text);
    line-height: 120%;

    @include toPhone {
      font-size: 13px;
    }
  }

  &--cource {
    #{$origin}__title {
      position: relative;

      &::before {
        content: "Курс";
        position: absolute;
        transform: translateY(-100%);
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        color: var(--text);
      }

      &::after {
        content: "";
        top: 0;
        left: 0;
        position: absolute;
        transform: translate(-50%, -200%);

        @include size(16px);

        background-color: var(--light-blue);
        border-radius: 100%;
        opacity: 0.3;
      }
    }
  }

  &--right {
    max-width: 100%;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    gap: 20px;

    #{$origin}__image {
      @include size(214px);
      margin: 0;

      @include toTablet {
        @include size(150px);
      }
    }

    #{$origin}__content {
      text-align: end;
    }

    #{$origin}__container {
      text-align: end;
      display: flex;
      flex-direction: column-reverse;
    }

    @include toPhone {
      height: max-content;
      gap: 8px;

      #{$origin}__container {
        flex-direction: column;
      }
    }
  }

  &--left {
    flex-direction: row;

    #{$origin}__content,
    #{$origin}__container {
      text-align: start;
    }
  }
}
