.list-all-courses {
  display: flex;
  flex-direction: column;
  gap: 40px;
}



#courses {
  --numcourses: 8;
  view-timeline-name: --courses-element-scrolls-in-body;

  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

@mixin generate-course-indexes($num) {
  @for $i from 1 through $num {
    #course_#{$i} {
      --index: #{$i};
    }
  }
}

@include generate-course-indexes(8);

@supports (animation-timeline: view()) {
  @keyframes animation-course-scale {
    to {
      filter: 
        // blur(calc(1px * var(--reverse-index)))
        opacity(calc(var(--index) / var(--numcourses)));
      transform: translateY(calc(-20px * var(--reverse-index)))
        scale(calc(1.1 - calc(0.1 * var(--reverse-index))));
    }
  }

  .course {
    --index0: calc(var(--index) - 1); /* 0-based index */
    --reverse-index: calc(
      var(--numcourses) - var(--index0)
    ); /* reverse index */

    --reverse-index0: calc(
      var(--reverse-index) - 1
    ); /* 0-based reverse index */
  }
}

.course {
  position: sticky;

  --top-padding: 20px;
  
  @include toPhone {
    --top-padding: 5px;
  }

  top: calc(100px + ((var(--index) - 1) * var(--top-padding)));
}

.course__content {
  --start-range: calc(var(--index0) / var(--numcourses) * 100%);
  --end-range: calc((var(--index)) / var(--numcourses) * 100%);

  animation: linear animation-course-scale forwards;
  animation-timeline: --courses-element-scrolls-in-body;
  animation-range: exit-crossing var(--start-range) exit-crossing
  var(--end-range);

  @include toPhone {
    & .card-half-circle__image {
        height: 238px !important;
    }
  }
}
