.section-special-offer {
  &__container {
    transform: translateY(100px);
    
    & .card-icon {
      margin-bottom: 40px;

      @include toPhone {
        margin: 0;
      }
    }

    @include toLabtop {
      transform: none;
    }
  }

  &__last {
    margin-top: 46px;

    @include toPhone {
      margin: 0;
    }
  }

  @include toPhone {
    row-gap: 25px;

    &__decor {
      margin-bottom: 40px;
    }
  }
}
