.section-magazine {
  $origin: &;

  position: relative;
  row-gap: 60px;

  &__container {
    display: grid;
    grid-template-columns: 67% 33%;
  }

  &__main-card {
    position: sticky;
    top: 100px;

    &--fixed {
      position: relative;
      top: 0;
    }
  }

  @include toLabtop {
    &__container {
      grid-template-columns: 50% 50%;
    }

    &__main-card {
      top: 80px;
    }
  }

  @include toTablet {
    row-gap: 38px;
  }

  @include toPhone {
    row-gap: 25px;

    &__main-card {
      position: relative;
      top: 0;
    }

    &__container {
      display: flex;
      flex-direction: column-reverse;
      gap: 20px;
    }

    &__title {
      & .title__main-text {
        max-width: 267px;
      }
    }

    &__link {
      transform: translateY(-3px);
    }
  }
}
