.breadcrumbs {
  padding-bottom: 10px;
  border-bottom: 1px solid var(--light-blue-50p);

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__item {
    font-size: 18px;
    color: var(--text);

    &:not(:first-child)::before {
      content: "";
      display: inline-block;
      background-image: url("./images/icons/crop.svg");
      background-repeat: no-repeat;
      background-size: contain;

      height: 12px;
      width: 8px;
      margin-right: 10px;
    }

    &:last-child {
      color: var(--gray);
    }

    transition: all var(--transition-time);

    @include onLabtop {
      &:not(:last-child):hover {
        transform-origin: left;
        transform: scale(1.05);
        color: var(--light-blue);
      }
    }
  }

  @include toPhone {
    &__link {
      font-size: 16px;
    }
  }
}
