.card-half-circle {
  overflow: hidden;

  $origin: &;
  background: var(--bg);
  border-radius: 20px;
  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 10%);

  &__image {
    display: block;
    object-fit: cover;
  }

  &__container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__title {
    color: var(--dark-blue);
  }

  &__text {
    color: var(--text);
  }

  //  + + + + + + + + + + +

  &--cource {
    #{$origin}__title {
      position: relative;

      &::before {
        content: "Курс";
        position: absolute;
        transform: translateY(-100%);
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        color: var(--text);
      }

      &::after {
        content: "";
        top: 0;
        left: 0;
        position: absolute;
        transform: translate(-75%, -235%);

        @include size(16px);
        background-color: var(--light-blue);
        border-radius: 100%;
      }
    }
  }

  &--white {
    background: var(--white);
  }

  &--top {
    display: flex;
    flex-direction: column;

    width: 100%;
    max-width: 386px;
    min-height: 440px;

    #{$origin}__image {
      height: 248px;

      --circle-size: 300px;

      @include addMaskCircle(--circle-size, "botton");
    }

    #{$origin}__container {
      padding: 20px 24px 40px 40px;
    }

    #{$origin}__title {
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 28px;
      line-height: 100%;
    }

    #{$origin}__text {
      line-height: 120%;
    }

    @include toPhone {
      min-height: max-content !important;

      #{$origin}__image {
        height: 160px;
      }

      #{$origin}__title {
        font-size: 22px;
      }
      
      #{$origin}__text {
        font-size: 14px;
      }

      #{$origin}__container {
        padding: 16px 16px 24px;
      }
    }
  }

  &--top-mini {
    min-height: 185px;

    #{$origin}__image {
      height: 138px;

      --circle-size: 144px;

      @include addMaskCircle(--circle-size, "botton");
    }

    #{$origin}__container {
      padding: 12px 10px 15px;
    }

    #{$origin}__title {
      font-size: 16px;
    }
  }

  &--mini-block {
    width: 100%;
    max-width: 350px;
    min-height: 376px;

    @include toLabtop {
      #{$origin}__container {
        padding: 20px 14px 30px;
      }
    }
  }

  &--img-left {
    #{$origin}__image {
      object-position: left;
    }
  }

  &--img-right {
    #{$origin}__image {
      object-position: right;
    }
  }

  //  + + + + + + + + + + +

  &--left {
    display: flex;
    align-items: stretch;
    width: 100%;
    gap: 32px;

    #{$origin}__image {
      width: 100%;
      max-width: 374px;
      
      --circle-size: 360px;
      
      @include toDesktop {
        --circle-size: 290px;
        width: 100%;
        max-width: 270px;
      }

      @include addMaskCircle(--circle-size, "right");
    }

    #{$origin}__container {
      width: 100%;
      max-width: 280px;
      padding-block: 50px;
      place-self: center;
    }

    #{$origin}__title {
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 28px;
      line-height: 100%;
      color: var(--dark-blue);
    }

    #{$origin}__text {
      line-height: 120%;
    }
  }

  &--right {
    @extend .card-half-circle--left;
    flex-direction: row-reverse;
    text-align: end;

    #{$origin}__image {
      object-position: right;

      @include addMaskCircle(--circle-size, "left");
    }
  }

  &--mini-inline {
    max-height: 238px;
  }
}
