$-height: 42px;

.button {
  $origin: &;

  height: $-height;

  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0 26px;

  background: var(--light-blue);
  border: 1px solid var(--white);
  border-radius: 45px;

  box-shadow: 0 3px 4px 0 #1f211d0a;

  text-wrap: nowrap;
  text-decoration: none;
  cursor: pointer;

  transition: all var(--transition-time);

  &--only-text {
    justify-content: center;
  }

  &--arrow {
    flex-direction: row-reverse;

    #{$origin}__icon {
      filter: drop-shadow(0 0 2px var(--light-blue))
        drop-shadow(0 0 4px var(--light-blue));
    }
  }

  &__text {
    line-height: $-height;
    text-align: center;
    color: var(--white);
  }

  &__image {
    min-height: $-height;
    filter: drop-shadow(0 0 2px var(--light-blue))
      drop-shadow(0 0 4px var(--light-blue));
  }

  @include onLabtop {
    &:hover {
      background-color: var(--white);
      border: 1px solid var(--light-blue);

      #{$origin}__text {
        color: var(--light-blue);
      }
    }
  }
}
