.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 50px 60px;

  @include toPhone {
    padding: 0 15px 60px;
  }

  &__title {
    margin-bottom: 25px; text-align: center;
  }

  &__links {
    margin-bottom: 50px;
  }

  &__texts {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    row-gap: 20px;

    &__1 {
      text-align: left;

      @include toPhone {
        font-size: 14px;
      }
    }

    &__2 {
      text-align: center;

      @include toPhone {
        font-size: 14px;
        text-align: left;
      }
    }

    &__3 {
      text-align: right;

      @include toPhone {
        font-size: 14px;
        text-align: left;
      }
    }
  }
}
