.comment {
  display: flex;
  gap: 10px;

  padding: 20px 10px 20px 20px;
  width: 100%;
  max-width: 385px;

  border-radius: 20px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 4%);
  background: #fffc;

  &__icon {
    @include size(38px);
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: var(--dark-blue);

    margin-bottom: 10px;
  }

  &__body {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 114%;
    letter-spacing: 0.01em;

    & > b {
      font-weight: 500;
      font-size: 18px;
      color: var(--dark-blue);
    }
  }

  @include toLabtop {
    padding: 14px 34px 14px 20px;
  }
}
