.main-photo-circle {
  $origin: &;

  &,
  & * {
    @include flex-center;
    box-sizing: content-box;
    border-radius: 100%;
  }

  &__image {
    @include size(75%);
    @include no-event;
    object-fit: cover;
    box-shadow: 
      0 0 0 40px rgb(221 221 221 / 42%), 
      0 0 0 130px rgb(230 230 230 / 42%), 
      0 0 0 200px rgb(237 237 237 / 42%);
  }

  &--mini {
    #{$origin}__image {
      box-shadow: 
      0 0 0 14px rgb(221 221 221 / 42%), 
      0 0 0 66px rgb(230 230 230 / 42%), 
      0 0 0 100px rgb(237 237 237 / 42%);
    }
  }
}
