.navigation {
  &__list {
    display: flex;
    justify-content: space-between;
    gap: 25px;
  }

  &__item {
    position: relative;
    text-wrap: nowrap;

    &::before {
      content: "";
      display: block;
      height: 4px;
      background-color: var(--light-blue);
      border-radius: 6px;

      width: 0%;
      position: absolute;
      bottom: 0;
      left: 0;

      transition: width var(--transition-time);
    }

    &--active::before {
      content: "";
      width: 100%;
    }
  }

  &__link {
    display: inline-block;
    line-height: 92px;
    font-weight: 500;
    text-decoration: none;
    color: var(--dark-blue);

    &:link,
    &:visited {
      color: var(--dark-blue);
    }

    @include onLabtop {
      &:hover {
        color: var(--blue);
      }
    }
  }
}
