.section-service {
  position: relative;
  display: flex;
  flex-direction: column;

  gap: 20px;

  @include toTablet {
    padding-top: 0;
    margin-top: 40px;

    &__first {
      padding-right: 14px;
    }
  }

  @include toPhone {
    gap: 0;

    &__first {
      padding: 0;
    }
  }

  &__title {
    margin-block: 25px 52px;

    @include toDesktop {
      margin-block: 30px 32px;
    }

    @include toPhone {
      margin-block: 65px 25px;
    }
  }

  &__footer {
    align-items: flex-start;

    
    @include toLabtop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      
      & > * {
        place-self: center;
      }
      
      & > article {
        width: 100%;
        max-width: 100%;
        min-width: none;
        place-self: start;
      }
    }

    @include toPhone {
      margin-top: 14px;
      display: flex;
      flex-direction: column;
    }
  }

  &__button {
    margin-top: 20px;

    @include toDesktop {
      justify-content: end;
      // padding-inline: 100px;
      margin: 0;
    }
  }

  &__cards-with-circle {
    @include flex-center;
    position: relative;

    & > article {
      place-self: start;
    }

    gap: 20px;

    @include toDesktop {
      gap: 14px;
    }
  }
}
