// @use "./breakpoints" as *;

@mixin toDesktop {
  @media (max-width: #{map-get($breakpoints, desktop) + px}) {
    @content;
  }
}

@mixin toLabtop {
  @media (max-width: #{map-get($breakpoints, labtop) + px}) {
    @content;
  }
}

@mixin toTablet {
  @media (max-width: #{map-get($breakpoints, tablet) + px}) {
    @content;
  }
}

@mixin toPhablet {
  @media (max-width: #{map-get($breakpoints, phablet) + px}) {
    @content;
  }
}

@mixin toPhone {
  @media (max-width: #{map-get($breakpoints, phone) + px}) {
    @content;
  }
}
