.card-tariffs {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  $gap: 23px;
  background-color: var(--white);
  border-radius: 30px;
  border: 1px solid var(--light-blue);
  padding-bottom: 40px;
  overflow: hidden;

  &__head {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: relative;
    text-align: center;
    line-height: 100%;
    margin-bottom: $gap;
    padding-top: 48px;
    padding-bottom: 38px;
    font-family: var(--second-family);
  }

  &__body {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    gap: 40px;
  }

  &__bg {
    @include size(494px);

    position: absolute;
    border-radius: 100%;
    background-color: #ffe7f1;
    bottom: 0;
  }

  &__title {
    position: relative;

    font-size: 36px;
    color: var(--dark-blue);

    margin-bottom: 35px;
  }

  &__prices {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }


  &__price {
    position: relative;
    font-weight: 700;
    font-size: 46px;
    color: var(--dark-blue);
  }

  &__old-price {
    position: relative;
    font-size: 24px;
    text-decoration: line-through;
    color: var(--text);
    opacity: 0.5;
  }

  &__list {
    padding-inline: 25px;
    display: flex;
    flex-direction: column;
    gap: $gap;
  }

  &__item {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.02em;
    color: var(--text);

    padding-bottom: $gap;

    border-bottom: 1px solid #e0e6ed;
  }

  &__button {
    padding-inline: 68px;
    bottom: 0;
  }

  @include toPhone {
    &__head {
      padding-top: 24px;
    }

    &__title {
      font-size: 26px !important;
      margin-bottom: 20px;
    }

    &__prices {
      flex-direction: row-reverse;
      justify-content: center;
      align-items: center;
      gap: 10px;
    }

    &__button {
      padding: 0;
      margin: auto;
    }
  }

  &--pro {
    #{$origin}__bg {
      background-color: #ebe7ff;
    }
  }

  &--best {
    box-shadow: 0 0 55px 0 rgb(150 182 220 / 80%);
    background-color: var(--light-blue);
    background: linear-gradient(180deg, #f6cadc 5%, #96b6dc 25%);

    #{$origin}__head {
      padding-top: 45px;
      padding-bottom: 50px;

      &::before {
        content: "Найкраща пропозицiя";
        display: flex;
        align-items: end;
        
        position: absolute;
        
        background-color: var(--light-blue);
        border-radius: 28px;
        width: 180px;
        height: 110px;
        padding: 0 49px 7px 0;
        top: -55px;
        left: -50px;
        z-index: 1;
        border: 1px solid #ffd1e4;

        font-family: var(--font-family);
        font-weight: 500;
        font-size: 15px;
        line-height: 100%;
        text-align: center;
        color: var(--white);

        transform: rotate(-45deg);
      }
    }

    #{$origin}__bg {
      background: #f6cadc;
      box-shadow: 0 80px 150px -40px var(--light-blue)
    }

    #{$origin}__title {
      font-size: 46px;
    }

    #{$origin}__price {
      font-size: 52px;
    }

    #{$origin}__item {
      color: var(--white);
      border-color: var(--white);
    }
  }
}
