@mixin gridSetting {
  display: grid;
  grid-template-columns: 18% 26% 6% 26% 11% 13%;

  @include toDesktop {
    grid-template-columns: 50% 5% 45%;
  }

  @include toLabtop {
    grid-template-columns: 50% 50%;
  }

  @include toPhone {
    row-gap: 14px;
  }
}

@mixin gridItemSetting() {
  // height: max-content;
}

@mixin generateGridColumns($columns) {
  @for $start from 1 through $columns {
    @for $end from $start through $columns {
      &-#{$start}-#{$end} {
        grid-column: #{$start} / #{$end + 1};
      }
    }
  }

  &-a {
    grid-column: auto / auto;
  }
}

@mixin generateGridRows($rows) {
  @for $start from 1 through $rows {
    @for $end from $start through $rows {
      &-#{$start}-#{$end} {
        grid-row: #{$start} / #{$end + 1};
      }
    }
  }

  &-a {
    grid-row: auto / auto;
  }
}

._grid {
  @include gridSetting;

  &-col {
    &--desk {
      @include generateGridColumns(6);
    }

    &--lab {
      @include toDesktop {
        @include generateGridColumns(3);
      }
    }

    &--tab {
      @include toLabtop {
        @include generateGridColumns(2);
      }
    }

    &--phone {
      @include toPhone {
        @include generateGridColumns(2);
      }
    }
  }

  $-rows: 10;

  &-row {
    &--desk {
      @include generateGridRows($-rows);
    }

    &--lab {
      @include toDesktop {
        @include generateGridRows($-rows);
      }
    }

    &--tab {
      @include toLabtop {
        @include generateGridRows($-rows);
      }
    }

    &--phone {
      @include toPhone {
        @include generateGridRows($-rows);
      }
    }
  }
}

@mixin generateOrders() {
  @for $index from -10 through 10 {
    &-#{$index} {
      order: $index;
    }
  }
}

._order {
  &-desk {
    @include generateOrders;
  }

  &-lab {
    @include toDesktop {
      @include generateOrders;
    }
  }

  &-tab {
    @include toLabtop {
      @include generateOrders;
    }
  }

  &-phone {
    @include toPhone {
      @include generateOrders;
    }
  }
}

@mixin place-self-symbol() {
  &-↙ {
    place-self: end left;
  }

  &-↓ {
    place-self: end center;
  }

  &-↘ {
    place-self: end right;
  }

  &-↖ {
    place-self: start left;
  }

  &-↑ {
    place-self: start center;
  }

  &-↗ {
    place-self: start right;
  }

  &-← {
    place-self: center left;
  }

  &-→ {
    place-self: center right;
  }

  &-⭱ {
    place-self: start auto;
  }

  &-↕ {
    place-self: center auto;
  }

  &-⭳ {
    place-self: end auto;
  }

  &-⭰ {
    place-self: auto left;
  }

  &-⭤ {
    place-self: auto center;
  }

  &-⭲ {
    place-self: auto right;
  }

  &-s {
    place-self: stretch;
  }

  &-a {
    place-self: auto;
  }

  &-c {
    place-self: center;
  }

  &-n {
    place-self: normal;
  }
}

._place {
  &-desk {
    @include place-self-symbol;
  }

  &-lab {
    @include toDesktop {
      @include place-self-symbol;
    }
  }

  &-tab {
    @include toLabtop {
      @include place-self-symbol;
    }
  }

  &-phone {
    @include toPhone {
      @include place-self-symbol;
    }
  }

}

/*
    ⭱
    ⭤
    ⭳

 ⭰  ↕  ⭲

  ↖ ↑ ↗
  ← x →
  ↙ ↓ ↘

  s - stretch 
  a - auto 
  n - normal
  c - center
*/
