.test-cursor {
  @include size(80px);
  @include no-event;
  cursor: none !important;
  transform: translate(-50%, -50%) scale(0.4);
  position: absolute;
  z-index: 1;

  border-radius: 100%;
  border: 1px solid var(--white);
  background-color: var(--light-blue-50p);

  transition: transform 0.1s ease-in;

  &--active {
    transform: translate(-50%, -50%) scale(1);
  }
}
