.decor {
  $origin: &;
  width: 100%;
  max-width: max-content;

  &__image {
    position: relative;

    &__photo {
      display: block;
      width: 409px;
      height: 451px;
      object-fit: cover;
      border-radius: 20px;

      @include shadow;
    }

    &__decor {
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translate(-50%, 50%);
      border-radius: 100%;

      @include size(296px);
      @include shadow;
    }

    @include toDesktop {
      &__photo {
        width: 364px;
        height: 400px;
      }

      &__decor {
        @include size(254px);
      }
    }

    @include toLabtop {
      &__photo {
        width: 246px;
        height: 275px;
      }

      &__decor {
        transform: translate(-60%, 30%);

        @include size(133px);
      }
    }

    @include toPhone {
      &__photo {
        width: 112px;
        height: 134px;
      }

      &__decor {
        transform: translate(0%, 50%);

        @include size(48px);
      }
    }
  }

  &--big {
    #{$origin}__image {
      @include toPhone {
        &__photo {
          width: 196px;
          height: 218px;
        }
  
        &__decor {
          @include size(114px);
          transform: translate(-35%, 50%);
        }
      }
    }
  }
}
