.section-article {
  &__title {
    margin-bottom: 30px;


  }

  &__texts {
    display: flex;
    justify-content: space-between;
    gap: 90px;

    margin-bottom: 30px;

    @include toPhone {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__second-title {
    font-family: var(--second-family);
    font-size: 28px;
    line-height: 28px;
    font-weight: 500;
    color: var(--dark-blue);
    margin-bottom: 40px;
  }

  &__advices {
    display: grid;
    grid-template-columns: 0.4fr 0.6fr;
    row-gap: 30px;

    @include toLabtop {
      display: flex;
      flex-direction: column;
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    gap:36px;
  }

  &__last {
    margin-top: 20px;

    display: grid;
    grid-template-columns: 0.5fr 0.5fr;
  }

  @include toPhone {
    &__advices {
      row-gap: 16px;
    }

    &__second-title {
      font-size: 26px;
    }

    &__last {
      display: block;
      margin-top: 30px;
    }
  }
}

.section-by-the-way {
  &__title {
    text-align: center;
    margin-bottom: 40px;

    @include toPhone {
      margin-bottom: 10px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include toDesktop {
      padding-right: 100px;
    }

    @include toLabtop {
      padding-left: 25px;
      padding-right: 0;
    }

    @include toPhone {
      padding: 0;
    }
  }

  &__image {
    @include toPhone {
      margin-bottom: -30px;
    }
  }
}

.section-importantly {
  &__title {
    text-align: start;
    margin-bottom: 40px;

    @include toPhone {
      margin-bottom: 10px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__image {
    @include toPhone {
      margin-bottom: -30px;
    }
  }
}
