._anim-items { transition: all 1s }
._anim-once { /* play animation one time */ }

._anim-duration-0_25 { transition: all 0.25s !important; animation-duration: 0.25s !important; }
._anim-duration-0_5 { transition: all 0.5s !important; animation-duration: 0.5s !important; }
._anim-duration-1 { transition: all 1s !important; animation-duration: 1s !important; }
._anim-duration-1_5 { transition: all 1.5s !important; animation-duration: 1.5s !important; }
._anim-duration-2 { transition: all 2s !important; animation-duration: 2s !important; }
._anim-duration-2_5 { transition: all 2.5s !important; animation-duration: 2.5s !important; }
._anim-duration-3 { transition: all 3s !important; animation-duration: 3s !important; }
._anim-duration-3_5 { transition: all 3.5s !important; animation-duration: 3.5s !important; }

._anim-delay-0_25 { animation-delay: 0.25s !important; transition-delay: 0.25s !important; }
._anim-delay-0_5 { animation-delay: 0.5s !important; transition-delay: 0.5s !important }
._anim-delay-0_75 { animation-delay: 0.75s !important; transition-delay: 0.75s !important }
._anim-delay-1 { animation-delay: 1s !important; transition-delay: 1s !important }
._anim-delay-1_25 { animation-delay: 1.25s !important; transition-delay: 1.25s !important }
._anim-delay-1_5 { animation-delay: 1.5s !important; transition-delay: 1.5s !important }
._anim-delay-1_75 { animation-delay: 1.75s !important; transition-delay: 1.75s !important }
._anim-delay-2 { animation-delay: 2s !important; transition-delay: 2s !important }
._anim-delay-2_5 { animation-delay: 2.5s !important; transition-delay: 2.5s !important }
._anim-delay-3 { animation-delay: 3s !important; transition-delay: 3s !important }
._anim-delay-3_5 { animation-delay: 3.5s !important; transition-delay: 3.5s !important }

// Transitions

._transition-to-top {
  opacity: 0;
  transform: translateY(100px);

  &._anim-active {
    opacity: 1;
    transform: none;
  }
}

._transition-to-bottom {
  opacity: 0;
  transform: translateY(-100px);

  &._anim-active {
    opacity: 1;
    transform: none;
  }
}

._transition-left {
  opacity: 0;
  transform: translateX(-100px);

  &._anim-active {
    opacity: 1;
    transform: none;
  }
}

._transition-right {
  opacity: 0;
  transform: translateX(100px);

  &._anim-active {
    opacity: 1;
    transform: none;
  }
}

._transition-scale {
  opacity: 0;
  transform: scale(0);

  &._anim-active {
    opacity: 1;
    transform: scale(1);
  }
}

._transition-clip-form-left {
  transition: all 2.5s ease;
  clip-path: inset(0 100% 0 0);

  &._anim-active {
    clip-path: inset(0 0 0 0);
  }
}

._transition-clip-form-right {
  opacity: 0;
  transition: all 2.5s ease;
  clip-path: inset(0 0 0 100%);

  &._anim-active {
    opacity: 1;
    clip-path: inset(0 0 0 0);
  }
}

._transition-opacity {
  opacity: 0;
  
  &._anim-active {
    opacity: 1;
  }
}

._transition-clip-circle {
  opacity: 0;
  clip-path: circle(0% at 50% 50%);
  transition-duration: 2s;
  transition-timing-function: cubic-bezier(.62,.45,.39,1.42);
  filter: drop-shadow(0 0 200px var(--light-blue));
  
  &._anim-active {
    opacity: 1;
    filter: none;
    clip-path: circle(100% at 50% 50%);
  }
}

._transition- {
  &._anim-active {
  }
}

// Animations

/* ----------------------------------------------------- */
// Random Moving

$speed: 10s;

.at-ramdom-moving-0 {
  -webkit-animation: ramdom-moving $speed infinite;
  animation: ramdom-moving $speed infinite;
  animation-timing-function: ease;
  animation-delay: 0s;
}

.at-ramdom-moving-1 {
  -webkit-animation: ramdom-moving $speed infinite;
  animation: ramdom-moving $speed infinite;
  animation-timing-function: ease;
  animation-delay: -1s;
}

.at-ramdom-moving-2 {
  -webkit-animation: ramdom-moving $speed infinite;
  animation: ramdom-moving $speed infinite;
  animation-timing-function: ease;
  animation-delay: -2s;
}

.at-ramdom-moving-3 {
  -webkit-animation: ramdom-moving $speed infinite;
  animation: ramdom-moving $speed infinite;
  animation-timing-function: ease;
  animation-delay: -3s;
}

@keyframes ramdom-moving {
  $size: 2px;
  $mult: 1;

  // transform: translate(0, ($size * $mult)); // ↓
  // transform: translate(0, (-$size * $mult)); // ↑
  // transform: translate((-$size * $mult), 0); // ←
  // transform: translate(($size * $mult), 0); // →

  0% {
    transform: translate($size, -$size); // ↗
    scale: 1;
  }

  13% {
    transform: translate(0, 0);
  }

  26% {
    transform: translate(-$size, -$size); // ↖
  }

  39% {
    transform: translate(0, 0);
  }

  52% {
    transform: translate(-$size, $size); // ↙
    scale: 1.05;
  }

  65% {
    transform: translate(0, 0);
  }

  78% {
    transform: translate($size, $size); // ↘
  }

  91% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate($size, -$size); // ↗
    scale: 1;
  }
}

/* ----------------------------------------------------- */
// Random Rotate

.at-ramdom-rotate-0 {
  -webkit-animation:  ramdom-rotating  $speed infinite;
  animation: r ramdom-rotating  $speed infinite;
  animation-timing-function: ease;
  animation-delay: 0s;
}

.at-ramdom-rotate-1 {
  -webkit-animation:  ramdom-rotating  $speed infinite;
  animation:  ramdom-rotating  $speed infinite;
  animation-timing-function: ease;
  animation-delay: -1s;
}

.at-ramdom-rotate-2 {
  -webkit-animation:  ramdom-rotating  $speed infinite;
  animation:  ramdom-rotating  $speed infinite;
  animation-timing-function: ease;
  animation-delay: -2s;
}

.at-ramdom-rotate-3 {
  -webkit-animation:  ramdom-rotating  $speed infinite alternate;
  animation:  ramdom-rotating  $speed infinite alternate;
  animation-timing-function: ease;
  animation-delay: -3s;
}

@keyframes ramdom-rotating {
  0% {
    transform: rotate(0deg);
  }
  
  20% {
    transform: rotate(-2deg);
  }

  50% {
    transform: rotate(4deg);
  }
  
  85% {
    transform: rotate(1deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

/* ----------------------------------------------------- */
// Shadow-Puls

._animation-box-shadow {
  animation-name: animation-box-shadow;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: none;
}

@keyframes animation-box-shadow {
  0% {
    box-shadow: 0 0 0 46px hsla(213, 50%, 73%, 0.05), 0 0 0 105px hsla(213, 50%, 73%, 0.03);
  }

  100% {
    box-shadow: 0 0 0 16px hsla(213, 50%, 73%, 0.1), 0 0 0 55px hsla(213, 50%, 73%, 0.06);
  }
}


/* ----------------------------------------------------- */
// Jelly

._animation-jelly {
  animation: none;

  &._anim-active {
    animation-name: rubberband-horizontal;
    animation-duration: 1.25s;
    animation-timing-function: linear;
    animation-delay: 0.25s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: none;
  }

  /* shorthand
		animation: rubberband-horizontal 1s linear 0s 1 normal none; */
}

@keyframes rubberband-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    transform: scale3d(0.75, 1.25, 1);
  }

  50% {
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    transform: scale3d(1.05, 0.95, 1);
  }

  100% {
    transform: scale3d(1, 1, 1);
  }
}

/* ----------------------------------------------------- */
._animation-rotate {
  animation: none;

  &._anim-active {
    animation-name: rotate;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: none;
  }
}

@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }

  30% {
    transform: rotateZ(30deg);
  }

  60% {
    transform: rotateZ(-30deg);
  }

  100% {
    transform: rotateZ(0deg);
  }
}

/* ----------------------------------------------------- */
._animation-bounce {
  animation: none;

  &._anim-active {
    animation-name: bounce-top;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: none;
  }
}

@keyframes bounce-top {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-24px);
  }

  55% {
    transform: translateY(0);
  }

  65% {
    transform: translateY(-12px);
  }

  75% {
    transform: translateY(0);
  }

  82% {
    transform: translateY(-6px);
  }

  87% {
    transform: translateY(0);
  }

  93% {
    transform: translateY(-4px);
  }

  100% {
    transform: translateY(0);
  }
}

/* ----------------------------------------------------- */

._animation-wobble-hor-top {
  animation: none;

  &._anim-active {
    animation-name: wobble-hor-top;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: none;
  }

  /* shorthand
		animation: wobble-hor-top 1s linear 0s 1 normal none; */
}

@keyframes wobble-hor-top {
  0% {
    transform: translateX(0%);
    transform-origin: 50% 50%;
  }

  15% {
    transform: translateX(-20px) rotate(3deg);
  }

  30% {
    transform: translateX(10px) rotate(-3deg);
  }

  45% {
    transform: translateX(-10px) rotate(2.6deg);
  }

  60% {
    transform: translateX(6px) rotate(-1.4deg);
  }

  75% {
    transform: translateX(-3px) rotate(0.2deg);
  }

  100% {
    transform: translateX(0%);
    transform-origin: 50% 50%;
  }
}
