.video-link {
  $origin: &;

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    color: var(--dark-blue);
    margin-bottom: 10px;
  }

  &__view {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 3px 4px 0 #1f211d0a;
    cursor: pointer;

    transition: all var(--transition-time);

    @include onLabtop {
      &:hover {
        transform: scale(1.05);
        box-shadow: 0 6px 8px 2px #1f211d3c;

        #{$origin}__button {
          transform: translate(-50%, -50%) scale(1.2);
          background-color: #fff6;
          box-shadow: 0 0 20px 0 white;
          z-index: 2;
        }

        #{$origin}__image {
          filter: blur(2px) brightness(85%);
          -webkit-filter: blur(2px) brightness(85%);
          -moz-filter: blur(2px) brightness(85%);
          z-index: 1;
        }
      }
    }
  }

  &__button {
    @include size(40px);

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    outline: none;
    border-radius: 100%;
    border: 1px solid #ffffff80;
    background-color: #fff3;
    color: white;

    transition: all var(--transition-time);
    z-index: 2;

    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);

    &::after {
      content: "";
      display: block;
      width: 12px;
      height: 15px;
      background-image: url("./images/icons/play.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;

      position: absolute;
      top: 50%;
      left: 53%;

      transform: translate(-50%, -50%);
    }
  }

  &__image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;

    z-index: 1;

    transition: all var(--transition-time);
  }
}
