.medium-circle {
  @include size(1042px);
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding-top: 56px;
  padding-bottom: 32px;
  gap: 14px;

  &__fon {
    @include no-event;
    @include size(1042px);
    position: absolute;
    background-color: white;
    overflow: hidden;
    box-shadow: inset 0 0 10px 30px white;

    border-radius: 100%;
    z-index: -1;
    top: 0;

    &::after {
      @include fullscreen;
      content: "";
      filter: blur(150px);
      background: linear-gradient(
        360deg,
        rgb(150 182 220 / 15%) 0%,
        rgb(166 239 221 / 15%) 100%
      );
    }
  }

  &__container {
    width: 500px;

    &__block {
      margin: 36px auto 50px;
    }
  }

  &__text {
    margin-top: 44px;
  }

  &__buttom {
    margin-top: -8px;
  }

  @include toPhone {
    width: 100%;
    height: 876px;
    // padding-inline: 15px;
    padding-top: 44px;
    padding-bottom: 32px;

    &__fon {
      @include size(876px);
    }

    &__container {
      width: 100%;

      &__block {
        margin: 16px auto 0;
      }
    }

    &__text {
      text-align: start;
      margin-top: 22px;
    }

    & .card-icon__image {
      @include size(30px);
    }
  }
}
