.button-circle {
  $origin: &;
  @include size(200px);
  display: flex;
  background-color: var(--light-blue);
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 100%;
  font-weight: 500;
  font-size: 19px;

  z-index: 1;
  position: relative;

  @include shadow;
  
  &__container {
    @include flex-center;
    @include size(100%);
    gap: 6px;
    transform: rotate(-15deg);
  }

  &__text {
    display: flex;
    flex-direction: column;
    width: max-content;
    text-align: center;
  }

  &__image {

    filter: drop-shadow(0 0 2px var(--light-blue))
      drop-shadow(0 0 4px var(--light-blue));
  }

  transition: all var(--transition-time);

  @include onLabtop {
    &:hover {
      color: var(--light-blue);
      background-color: var(--white);
      border: 1px solid var(--light-blue);
    }
  }

  @include toLabtop {
    @include size(180px);
  }

  @include toPhone {
    @include size(150px);
    font-size: 16px;
  }

  &--big {
    @include size(260px);

    #{$origin}__container {
      transform: rotate(-30deg);
    }

    @include toLabtop {
      @include size(200px);
    }

    @include toPhone {
      @include size(180px);
      font-size: 16px;
    }
  }

  // &--medium {
  //   @include size(240px);

  //   @include toLabtop {
  //     @include size(200px);
  //     font-size: 16px;
  //   }
  // }
}
