.title--circle-number {
  display: flex;
  flex-direction: column-reverse;
  justify-content: start;
  place-self: start;
  text-align: start;
  border-bottom: 1px solid var(--light-blue-80p);
  padding-bottom: 38px;
  height: 100%;

  &__number {
    @include shadow;
    @include size(68px);
    font-family: var(--second-family);
    background-color: var(--white);
    border-radius: 100%;

    text-align: center;
    line-height: 120%;
    font-weight: 500;
    font-size: 45px;
    color: var(--light-blue-50p);

    margin-bottom: 22px;
  }

  &__main-text {
    font-family: var(--second-family);
    font-size: 28px;
    line-height: 28px;
    color: var(--dark-blue);

    margin-bottom: 18px;
  }

  &__text {
    font-size: 16px;
    color: var(--text);
  }

  &__container {
    display: flex;
    flex-direction: column-reverse;
  }

  @include toPhone {
    padding-bottom: 15px;

    &__number {
      @include size(48px);
      font-size: 26px;
      line-height: 140%;
    }

    &__main-text {
      font-size: 20px;
      margin-bottom: 10px;
    }

    &__text {
      font-size: 14px;
    }

    &__container {
      flex-direction: row-reverse;
      gap: 12px;
      justify-content: start;
      align-items: center;
    }
  }
}
