@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;

  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin shadow {
  box-shadow: 0 3px 10px 0 rgb(31 33 29 / 7%);
}

@mixin fullscreen {
  position: absolute;
  bottom: 0;
  top: 0;
  right: 0;
  left: 0;
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin size($_size) {
  width: #{$_size};
  height: #{$_size};
}

@mixin no-event {
  pointer-events: none;
  -webkit-user-select: none;
  user-select: none;
}

// Effects
@mixin addMaskCircle($-circle-size, $-dirrection: "right") {
  @if $-dirrection == "right" {
    clip-path: circle(
      var($-circle-size) at calc((var($-circle-size) - 100%) * -1) 50%
    );
  } @else if $-dirrection == "left" {
    clip-path: circle(var($-circle-size) at var($-circle-size) 50%);
  } @else if $-dirrection == "top" {
    clip-path: circle(var($-circle-size) at 50% var($-circle-size));
  } @else if $-dirrection == "botton" {
    clip-path: circle(
      var($-circle-size) at 50% calc((var($-circle-size) - 100%) * -1)
    );
  }
}

// Fonts

// Пример функции для размеров в rem
@function clm_rem($min, $width, $max) {
	$min_rem: #{$min + rem};
	$max_rem: #{$max + rem};
	$width_vw: #{calc(round(($max * 16) / ($width / 100) * 100000) / 100000) + vw};
	@return clamp($min_rem, $width_vw, $max_rem);
}

// Пример подключения функции, если максимальный шрифт 2rem, 
// минимальный 1.5rem и он должен уменьшаться, 
// когда разрешение экрана меньше 600px

// font-size: clm_rem(1.5, 600, 2);

// Для размеров в px
@function clm_px($min, $width, $max) {
	$min_px: #{$min + px};
	$max_px: #{$max + px};
	$width_vw: #{calc(round($max / ($width / 100) * 100000) / 100000) + vw};
	@return clamp($min_px, $width_vw, $max_px);
}

// Подключаем функцию для уменьшения отступов в блоке. 
// Максимальный отступ 30px, минимальный 20px. 
// Отступ должен уменьшаться при разрешении меньше 600px

// padding: clm_px(20, 600, 30);

/* -------------------------------------------------- */

@mixin position-transform-symbol() {
  &-↙ {
    position: absolute;
    top: initial;
    right: initial;
    bottom: 0;
    left: 0;
    transform: none;
  }

  &-↓ {
    position: absolute;
    top: initial;
    right: initial;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &-↘ {
    position: absolute;
    top: initial;
    right: 0;
    bottom: 0;
    left: initial;
    transform: none;
  }

  &-↖ {
    position: absolute;
    top: 0;
    right: initial;
    bottom: initial;
    left: 0;
    transform: none;
  }

  &-↑ {
    position: absolute;
    top: 0;
    right: initial;
    bottom: initial;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &-↗ {
    position: absolute;
    top: 0;
    right: 0;
    bottom: initial;
    left: initial;
    transform: none;
  }

  &-← {
    position: absolute;
    top: 50%;
    right: initial;
    bottom: initial;
    left: 0;
    transform: translate(0, -50%);
  }

  &-→ {
    position: absolute;
    top: 50%;
    right: 0;
    bottom: initial;
    left: initial;
    transform: translate(0, -50%);
  }

  &-c {
    position: absolute;
    top: 50%;
    right: initial;
    bottom: initial;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &-n {
    position: absolute;
    top: initial;
    right: initial;
    bottom: initial;
    left: initial;
    transform: none;
  }
}

.screen {
  @include fullscreen;

  &__container {
    @include size(100%);
    position: relative;

    & > * {
      position: absolute;
    }
  }

  &-desk {
    @include position-transform-symbol;
  }

  &-lab {
    @include toDesktop {
      @include position-transform-symbol;
    }
  }

  &-tab {
    @include toLabtop {
      @include position-transform-symbol;
    }
  }

  &-phone {
    @include toPhone {
      @include position-transform-symbol;
    }
  }
}
