.carusel {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1070px;
  width: 100%;

  position: relative;

  &__text {
    background-color: transparent;
    resize: none;
    overflow: hidden;
    -webkit-mask-image: linear-gradient(#000, 80%, #00000090);
    mask-image: linear-gradient(#000, 80%, #00000090);

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 129%;
    text-align: center;
    color: var(--text);

    max-width: 600px;
    width: 100%;
    margin-bottom: 48px;
  }

  &__buttons {
    position: absolute;
    top: 55%;
    display: flex;
    justify-content: space-between;
    width: 80%;
  }

  &__list {
    position: relative;
    width: 100%;
    height: 276px;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: grab;
    -webkit-animation: carusel-item-moving 1s infinite alternate;
    animation: carusel-item-moving 1s infinite alternate;
    transform-origin: center;

    position: absolute;
    top: 0;
    left: 0;

    filter: blur(3px) drop-shadow(0 0 0 transparent);

    transition: all 0.7s !important;

    z-index: 10;

    & > * {
      @include no-event;
    }

    @include onLabtop {
      &:hover {
        filter: blur(0) drop-shadow(0 10px 10px var(--gray));
        animation-play-state: paused;
      }
    }

    &--2 {
      @include no-event;

      filter: none !important;
      animation-delay: 0.7s;
      top: 20%;
      left: 50%;
      z-index: 11;

      & .card-person__title {
        z-index: -1;
        text-shadow: 0 0 20px var(--bg), 0 0 20px var(--bg), 0 0 20px var(--bg),
          0 0 20px var(--bg), 0 0 20px var(--bg), 0 0 10px var(--bg),
          0 0 10px var(--bg), 0 0 10px var(--bg), 0 0 10px var(--bg),
          0 0 10px var(--bg);
      }
    }

    &--0 {
      animation-delay: 0.5s;
      top: -10%;
      left: 10%;
    }

    &--4 {
      animation-delay: 0.5s;
      top: -10%;
      left: 90%;
    }

    &--1 {
      top: 60%;
      left: 25%;
    }

    &--3 {
      top: 60%;
      left: 75%;
    }
  }
}

@keyframes carusel-item-moving {
  $size: 4px;

  from {
    transform: translateY($size) translateX(-50%);
  }

  to {
    transform: translateY(-$size) translateX(-50%);
  }
}

.carusel__button {
  @include shadow;

  display: flex;
  justify-content: center;
  align-items: center;

  $size: 40px;
  width: $size;
  height: $size;

  background-color: var(--white);
  border: 1px solid var(--light-blue);
  border-radius: 50%;

  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;

  opacity: 0.7;
  z-index: 12;

  & > svg {
    fill: var(--light-blue);
    height: 1.2em;
    width: 1.2em;
  }

  &--left {
    & > svg {
      transform: scaleX(-1);
    }
  }

  transition: background-color 0.1s, box-shadow 0.2s, border 0.2s, opacity 0.2s;

  @include onLabtop {
    &:hover {
      opacity: 1;
      box-shadow: 0 0 40px -5px var(--light-blue);
    }

    &:active {
      transform: scale(0.9);
      background-color: var(--light-blue);
      color: var(--white);
      border: 6px solid var(--light-blue);

      & > svg {
        fill: var(--white);
      }
    }
  }
}
