.card-big {
  $origin: &;
  max-width: 410px;
  width: 100%;

  &__image {
    display: block;
    width: 100%;
    object-fit: cover;

    border-radius: 20px;
    margin-bottom: 40px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 100%;

    color: var(--dark-blue);

    margin-bottom: 20px;
  }

  &__text {
    color: var(--text);
    line-height: 120%;
  }

  &--x2 {
    #{$origin}__image {
      height: 450px;
      border-radius: 30px;
      
      @include toLabtop {
        width: 290px;
        height: 340px;
      }
    }
  }
}
