.section-proposition {
  position: relative;
  margin-bottom: -300px;

  @include toDesktop {
    margin-bottom: -30px;
  }

  @include toLabtop {
    margin-bottom: -200px;
  }

  @include toPhone {
    margin-bottom: -300px;
  }

  &__info {
    margin-bottom: -180px;

    @include toLabtop {
      margin-bottom: -240px;
    }
    
    @include toPhone {
      margin-bottom: -100px;
    }
  }

  &__container {
    gap: 40px 0;
  }

  &__card {
    margin-top: 87px;

    @include toDesktop {
      margin: 0;
    }
  }

  @include toPhone {
    margin: 0;
    margin-top: -40px;

    &__container {
      gap: 20px 0;

      .card-half-circle__image {
        height: 300px;
      }

      .card-mini__image {
        margin-bottom: 35px;
      }
    }
  }
}
