.section-special-program {
  position: relative;
  margin-bottom: -255px;
  padding-bottom: 200px;

  &__list {
    gap: 40px 80px;
    grid-template-columns: repeat(2, 1fr);
    
    @include toDesktop {
      grid-template-columns: repeat(3, 1fr);
    }
    
    @include toLabtop {
      grid-template-columns: repeat(2, 1fr);
    }

    @include toPhone {
      grid-template-columns: 1fr;
    }
  }

  &__decor {
    z-index: -1;
    margin: 0;

    @include toDesktop {
      margin-top: -100px;
    }
    
    @include toLabtop {
      margin-top: -130px;
      margin-bottom: 20px;
    }

    @include toPhone {
      margin-block: -20px 70px;
    }
  }
}

.section-instruction {
  @include flex-center;
  flex-direction: column;

  position: relative;
  margin-top: -60px;
  margin-bottom: 100px;

  @include toPhone {
    margin-bottom: 40px;
  }

  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin-bottom: 70px;
    
    @include toPhone {
      margin-bottom: 20px;
    }
  }

  &__title {
    max-width: 600px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 60px;

    @include toLabtop {
      grid-template-columns: repeat(3, 1fr);
    }
    
    @include toTablet {
      grid-template-columns: repeat(2, 1fr);
    }
    
    @include toPhone {
      grid-template-columns: repeat(1, 1fr);
      gap: 15px;
    }
  }

  &__button {
    @include toPhone {
      margin-top: 50px;
      margin-bottom: -50px;
    }
  }

  &::before {
    content: "";

    @include size(140vw);
    position: absolute;
    top: -48px;
    display: block;
    border-radius: 100%;
    background: var(--white);
    z-index: -3;
  }
}
