.pop-up {
  $origin: &;
  position: fixed;
  inset: 0;
  z-index: 102;
  overflow-y: scroll;

  display: flex;
  flex-direction: column;
  height: 100dvh;

  transition: .5s;

  -webkit-backdrop-filter: blur(30px);
  backdrop-filter: blur(30px);
  background: rgba(245, 252, 255, 0.68);

  &--closed {
    opacity: 0;
    -webkit-backdrop-filter: blur(0);
    backdrop-filter: blur(0);
    background: transparent;
  }

  &__button {
    position: sticky;

    right: 0;
    top: 0;
    display: flex;
    justify-content: flex-end;
    
    padding: 34px 82px 0;
    z-index: 10;
    
    @include toPhone {
      padding: 16px 20px;
    }

    & > button {
      @include size(58px);
    }
  }

  &__content {
    position: relative;

    padding: 0 50px;

    @include toDesktop {
      padding: 0 20px;
    }

    @include toPhone {
      padding: 0 15px;
    }
  }

  &--fixed {
    overflow: hidden;

    #{$origin}__button {
      position: absolute;
    }

    #{$origin}__content {
      overflow: hidden;
      height: 100%;
      padding: 0;
    }
  }
}
