.title--module {
  display: flex;
  flex-direction: column-reverse;
  text-align: left;
  width: 100%;
  max-width: 265px;

  &__main-text {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: var(--light-blue);

    margin-bottom: 20px;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: var(--text);
  }

  &--right {
    text-align: right;
  }

  @include toPhone {
    &__main-text {
      font-size: 24px;
      margin-bottom: 15px;
    }

    &__text {
      font-size: 14px;
    }
  }
}
