// @use "./breakpoints" as *;

@mixin onDesktop {
  @media (min-width: #{map-get($breakpoints, labtop) + px}) {
    @content;
  }
}

@mixin onLabtop {
  @media (min-width: #{map-get($breakpoints, tablet) + px}) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: #{map-get($breakpoints, phablet) + px}) {
    @content;
  }
}

@mixin onPhablet {
  @media (min-width: #{map-get($breakpoints, phone) + px}) {
    @content;
  }
}

@mixin onPhone {
  @media (min-width: #{map-get($breakpoints, _zero) + px}) {
    @content;
  }
}
