.main-info {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: end;

  width: 30vw;
  height: 700px;

  @include toDesktop {
    height: 672px;
  }

  @include toLabtop {
    height: 580px;
  }

  @include toTablet {
    height: none;
  }

  &__photo {
    @include size(984px);
    transform: translateY(-144px);

    @include toDesktop {
      @include size(714px);
      transform: translate(10px, -90px);
    }

    @include toLabtop {
      @include size(494px);
      transform: translate(10px, -40px);
    }

    @include toPhablet {
      @include size(488px);
    }
  }

  &__container {
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: end;
    top: 336px;
    right: 0;
  }

  &__title {
    font-family: var(--second-family);
    color: var(--dark-blue);
    font-weight: 500;
    font-size: 26px;
    line-height: 100%;
    margin-bottom: 10px;
  }

  &__comments {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}
