.list-cards-course {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  text-align: center;
  padding-right: 20px;
  
  @include toLabtop {
    display: flex;
    flex-direction: column;
    align-items: center;

    & > * > * {
      max-width: 100%;
    }
  }
  
  @include toPhone {
    padding: 0;

    & > * > * {
      max-width: 400px;
    }
  }
}
