$width: 290px;

.decor-comments {
  position: relative;
  perspective: 1000px;

  &__image {
    max-width: 478px;
    max-height: 535px;
  }

  &__comment {
    &-position-1 {
      z-index: -1;
      position: absolute;
      top: -2%;
      left: -30%;
    }

    &-position-2 {
      z-index: 1;
      position: absolute;
      top: 10%;
      left: 50%;
      transform: translate(60%);
    }

    &-position-3 {
      z-index: 1;
      position: absolute;
      top: 50%;
      left: -12%;
    }

    &-position-4 {
      z-index: 1;
      position: absolute;
      top: 63%;
      left: 50%;
    }

    @include toDesktop {
      &-position-1 {
        z-index: 0;
        top: 45%;
        left: -55%;
      }

      &-position-3 {
        top: 75%;
      }
    }

    @include toLabtop {
      &-position-1 { 
        left: -15%; 
        top: 50%; 
      }

      &-position-2 { 
        top: 30%;
        left: 25%;
        max-width: 270px;
      }
    }

    @include toPhone {
      &-position-1 {
        width: $width;
        left: 20%;
        top: 90%;
        max-width: 80%;
        z-index: 1;
      }

      &-position-2 {
        width: $width - 10px;
        top: -15%;
        left: -20%;
        max-width: 75%;
        z-index: -1;
      }

      &-position-3 {
        width: $width - 30px;
        left: 18%;
        top: 50%;
        z-index: 1;
      }

      &-position-4 {
        width: $width - 60px;
        top: 65%;
        left: 30%;
        z-index: 1;
      }
    }

    &-color-1 {
      background: #ecf7cce5;
      max-width: max-content;
      padding: 24px 16px;
    }

    &-color-2 {
      background: #ebe7ffb2;
      max-width: max-content;
      padding: 24px 16px;
    }

    &-color-3 {
      background: #fff3b5e5;
      max-width: max-content;
      padding: 24px 16px;
    }

    &-color-4 {
      background: #ebf9ffe5;
      max-width: max-content;
      padding: 24px 16px;
    }
  }
}
