.contacts-menu {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: end;

  border-radius: 100px;

  -webkit-backdrop-filter: blur(22px);
  backdrop-filter: blur(22px);
  box-shadow: 0 0 20px 0 rgb(73 79 85 / 22%);
  background: rgb(255 255 255 / 80%);

  &__container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 6px auto;
    margin-bottom: 20px;
    opacity: 1;
    height: calc((20px + 56px) * 6 - 20px);

    @include toTablet {
      height: calc((20px + 38px) * 6 - 40px);
    }

    transition: opacity var(--transition-time), height var(--transition-time),
      margin var(--transition-time);

    &--hidden {
      opacity: 0;
      height: 0;
      margin: 0 auto;
      pointer-events: none;
    }
  }

  &__item {
    @include size(56px);
    position: relative;

    @include toTablet {
      @include size(38px);
    }
  }

  &__button {
    @include flex-center;
    @include size(68px);
    @include shadow;

    position: relative;
    z-index: 100;

    background-color: var(--white);
    border: 1px solid var(--light-blue);
    border-radius: 100%;
    cursor: pointer;

    @include toTablet {
      @include size(50px);
    }

    transition: filter var(--transition-time);

    &__image {
      @include size(32px);
      display: block;
      background-image: url("./images/icons/light__message.svg");
      background-size: contain;
      transition: all var(--transition-time);
      transform: rotate(0deg);
    }

    &--close {
      background-color: var(--bg);

      & > div {
        @include size(22px);
        background-image: url("./images/icons/light__close.svg");
        transform: rotate(360deg);
      }
    }

    @include onLabtop {
      &:hover {
        filter: drop-shadow(0 0 30px rgb(0 0 0 / 15%));
      }
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      border: 1px solid var(--light-blue);

      animation-name: contacts-menu-button-animation;
      animation-duration: 5s;
      animation-iteration-count: infinite;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      border: 1px solid var(--light-blue);

      animation-name: contacts-menu-button-animation;
      animation-duration: 5s;
      animation-delay: 0.5s;
      animation-iteration-count: infinite;
    }

    @keyframes contacts-menu-button-animation {
      0% {
        opacity: 0;
      }

      50% {
        opacity: 0;
      }

      80% {
        transform: scale(1.05);
        opacity: 1;
      }

      100% {
        transform: scale(1.65);
        opacity: 0;
      }
    }
  }
}

.button-icon {
  display: block;
  position: absolute;
  z-index: 10;
  border-radius: 100%;
  cursor: pointer;

  @include shadow;
  @include size(100%);

  @include onLabtop {
    &:hover {
      transform: scale(1.1);
    }
  }
}

[x-cloak] {
  display: none !important;
}
