
.date-timer {
  display: flex;
  justify-content: space-around;

  border-radius: 50px;
  width: 100%;
  max-width: 820px;
  height: 176px;
  padding-block: 32px 24px;
  padding-inline: 50px;

  box-shadow: 0 2px 10px 0 rgb(39 76 91 / 7%);
  background: linear-gradient(180deg, rgb(150 182 220 / 50%) 0%, #fff 100%);

  &__line {
    height: 100%;
    width: 1px;

    background: var(--gray);
    opacity: 0.3;
  }

  &__item {
    width: 100px;
    text-align: center;
    margin-top: 10px;
  }

  &__count {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 60px;
    line-height: 100%;
    color: var(--dark-blue);
    margin-bottom: 20px;
  }

  &__date-type {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-align: center;
    color: #2e383b;
  }

  @include toLabtop {
    height: 160px;
    border-radius: 30px;

    &__item {
      margin-top: 4px;
    }

    &__count {
      font-size: 44px;
    }

    &__date-type {
      font-size: 20px;
    }
  }

  @include toPhone {
    height: 96px;
    padding-block: 16px 12px;
    padding-inline: 8px;
    border-radius: 15px;

    &__item {
      width: 70px;
      margin-top: 0;
    }

    &__count {
      font-size: 26px;
    }
    
    &__date-type {
      font-size: 16px;
    }
  }
}
