.title {
  $origin: &;
  width: 100%;
  max-width: 700px;

  &__text {
    font-size: 20px;
    line-height: 88%;
    letter-spacing: 0.02em;
    margin-bottom: 18px;
  }

  &__main-text {
    font-family: var(--second-family);
    font-style: italic;
    font-weight: 400;
    font-size: 46px;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: var(--dark-blue);
  }

  @include toTablet {
    &__main-text {
      font-size: 36px;
    }
  }

  @include toPhone {
    &__text {
      font-size: 16px;
      line-height: 114%;
      letter-spacing: 0.02em;
    }

    &__main-text {
      font-size: 27px;
    }
  }

  &--second {
    #{$origin}__text {
      margin-bottom: 20px;
    }

    #{$origin}__main-text {
      font-size: 46px;
    }

    @include toTablet {
      #{$origin}__main-text {
        font-size: 36px;

        @include toPhone {
          font-size: 27px;
        }
      }
    }
  }

  &--one-text {
    #{$origin}__text {
      font-family: var(--second-family);
      color: var(--dark-blue);
      text-align: center;
      font-size: 22px;
      
      font-weight: 400;
      line-height: 120%;
      
      @include toPhone {
        font-size: 16px;
      }
    }
  }

  &--font-family {
    #{$origin}__main-text {
      font-style: normal !important;
    }
  }
}

@import "./title--mini";
@import "./title--module";
@import "./title--circle-number";
